.aboutTitle{
    position: absolute;
    color: $brand-white;
    font-size: 44px;
    font-family: 'Novecento Sans Web DemiBold';
    z-index: 1;
    top: 40%;
    left: 42.5%;
    text-transform: uppercase;
}

.homeLogo{
    position: absolute;
    z-index: 1;
    top: 30%;
    left: 37.5%;
}

.aboutContent{
    // z-index: -100;
    // margin-top: -200px;
    padding-bottom: 40px;
    margin-top: -42px;

    .heroWrapper{
        background: linear-gradient(
        rgba(0,0,0,0.25) 0%,
        rgba(0,0,0,0.25) 100%);
        height: 100vh;
        width: 100%;
        img {
            width: 100%;
            height: 100vh;
            position: relative;
            // object-fit: cover;
            object-position: center;
            z-index: -1;  
        }
    }
        h2{
            text-align: center;
            font-family: 'Novecento Sans Web DemiBold';
            text-transform: uppercase;
            font-size: 26px;
            padding: 30px 0;  
            color: $brand-purple
        }

        p{
        font-family: 'Merriweather Web Light';
        padding: 15px 160px;
        line-height: $brand-lineHeight;
        font-size: $brand-baseFontSize;
        color: $brand-baseFontColor;
        }
}